.sidebar {
    width: 100%;
    height: 60px;
    background-color: #DDEEFF;

}

.sidebar.open {
    width: 500px;
    height: 500px;
    background-color: black;
}

.sidebar.closes{
    width: 500px;
    height: 500px;
    background-color: tan;
}
.background {
    width: 1000px;
    height: 1000px;
    backdrop-filter: blur(2px);
    background: rgba(0, 0, 0, 0.3);
}

.nav {
    display: flex;
    flex-direction: column;
    row-gap : 45px;
    font-size: 22px;
}

.nav a {
    text-decoration: none;
    font-weight: 600;
    font-family: Montserrat;
    color: black;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    text-decoration:underline;
    color: #0059B2;
}
.close {
    height: 24px;
    width: 24px;
    border: none;
}
